<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full d-flex">
                            <h5 class="modal-title"> {{ $t('BillingConditions') }}</h5>
                            <button type="button" class="close" v-on:click="Close"> 
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body csscontrol_popup" style="padding: 0;">
                            <div class="container-fluid p-0 main-content">
                                <div class="row" style="margin: 0;">
                                    <!-- Right Column - Condition Builder -->
                                    <div class="right-content" style="padding: 20px;width: 100%;">
                                        <!-- Condition Name -->
                                        <div class="form-group mb-4">
                                            <label style="font-weight: 600; color: #495057; margin-bottom: 8px;">
                                                Condition Name:<span style="color: #dc3545;">*</span>
                                            </label>
                                            <input v-model="conditionName" :disabled="isEditMode"
                                                :class="['form-control', { 'is-invalid': showError && !conditionName }]"
                                                placeholder="Enter condition name"
                                                
                                                required />
                                            <div class="invalid-feedback text-danger small"
                                                v-if="showError && !conditionName">
                                                {{ $t('MandatoryFieldConditionName') }}
                                            </div>
                                        </div>
                                        <!-- Condition Logic Display -->
                                        <div class="form-group mb-4">
                                            <label style="font-weight: 600; color: #495057; margin-bottom: 8px;">Condition Logic:</label>
                                            <div style="background-color: #f8f9fa; border: 1px solid #dee2e6; border-radius: 4px; padding: 12px; min-height: 60px; color: #495057;">
                                                {{ conditionText }}
                                            </div>
                                        </div>
                                        
                                        <!-- Block-based Condition Builder -->
                                        <div class="condition-builder border">
                                            <div class="condition-block" v-for="(block, blockIndex) in conditionBlocks" :key="blockIndex">
                                                <!-- Operator between blocks (dynamic AND/OR) -->
                                                <div class="or-separator" style="text-align: center; margin: 5px 0;" v-if="blockIndex > 0">
                                                    <button type="button" class="btn btn-info" :disabled="isEditMode"
                                                            @click="toggleJoinOperator(blockIndex)"
                                                            style=" border: none; border-radius: 6px!important; font-weight: 600;">
                                                        {{ block.joinWithPrevious || 'OR' }}
                                                    </button>
                                                </div>
                                                
                                                <div class="block-header" style="display: flex; justify-content: space-between; align-items: center; margin-bottom: 5px;">
                                                    <h6 style="font-weight: 600; color: #495057; margin: 0;">Block {{ blockIndex + 1 }}</h6>
                                                    <div class="d-flex">
                                                        <button type="button" class="btn btn-sm btn-info"  :disabled="isEditMode"
                                                                :style="{ border: 'none', borderRadius: '8px!important', padding: '4px 12px', marginRight: '8px' }"
                                                                @click="toggleBlockOperator(blockIndex)">
                                                            {{ block.operator || 'AND' }}
                                                        </button>
                                                        <em class="fa fa-trash-o text-danger ml-2 font-24" @click="removeBlock(blockIndex)" v-if="conditionBlocks.length > 1"  v-show="!isEditMode"></em>
                                                    </div>
                                                </div>
                                                
                                                <div class="block-conditions">
                                                    <div class="condition-row" v-for="(row, i) in block.conditions" :key="i"
                                                         style="display: flex; align-items: center; margin-bottom: 5px; gap: 10px;">
                                                        <select v-model="row.field" class="form-control" style="flex: 1;"
                                                                @change="handleFieldChange(row)" :disabled="isEditMode">
                                                            <option value="" disabled>Select Field</option>
                                                            <optgroup v-for="(options, group) in groupedOptions" :label="group" :key="group">
                                                                <option v-for="option in options" :value="option.value" :key="option.value">
                                                                    {{ $t(option.label) }}
                                                                </option>
                                                            </optgroup>
                                                        </select>
                                                        
                                                        <select v-model="row.operator" class="form-control" style="flex: 0.5;"
                                                                :disabled="isEditMode" @change="handleDropdownValue(row)">
                                                            <option value="" disabled>Operator</option>
                                                            <option v-for="itm in row.operatorList || []" :key="itm.OPERATOR_ID" :value="itm.OPERATOR_ID">
                                                                {{ itm.OPERATOR_DISPLAY_NAME }}
                                                            </option>
                                                        </select>
                                                        
                                                        <div style="flex: 1; overflow: visible!important; min-width: 320px;" class="d-flex">
                                                            <!-- Date picker for date type -->
                                                            <v-date-picker mode="date"
                                                                v-if="row.sqlType === 'date' && !isValueHidden(row) && !isShowIsWeekDay(row)"
                                                                class="timedate input-group mr-2"
                                                                v-model="row.value">
                                                                <template v-slot="{ inputValue, togglePopover }">
                                                                    <input placeholder="MM/DD/YYYY"
                                                                        @click="togglePopover()"
                                                                        v-bind:class="{ 'form-control': true }"
                                                                        :value="inputValue" />
                                                                    <div @click="togglePopover()"
                                                                        class="input-group-append">
                                                                        <span class="input-group-text"><em class="fa fa-calendar"></em></span>
                                                                    </div>
                                                                </template>
                                                            </v-date-picker>
                                                            
                                                            <!-- Date picker for valueTo (Between/Date Range) -->
                                                            <v-date-picker mode="date"
                                                                v-if="row.sqlType === 'date' && !isValueHidden(row) && isValueToVisible(row) && !isShowIsWeekDay(row)"
                                                                class="timedate input-group"
                                                                v-model="row.valueTo">
                                                                <template v-slot="{ inputValue, togglePopover }">
                                                                    <input placeholder="MM/DD/YYYY"
                                                                        @click="togglePopover()"
                                                                        v-bind:class="{ 'form-control': true }"
                                                                        :value="inputValue" />
                                                                    <div @click="togglePopover()"
                                                                        class="input-group-append">
                                                                        <span class="input-group-text"><em class="fa fa-calendar"></em></span>
                                                                    </div>
                                                                </template>
                                                            </v-date-picker>
                                                            
                                                            <!-- Time picker for time type -->
                                                            <v-date-picker mode="time" v-model="row.value" 
                                                            v-if="row.sqlType === 'time' && !isValueHidden(row) && !isShowIsWeekDay(row)"
                                                                class="timedate input-group mr-2">
                                                                <template v-slot="{ inputValue, togglePopover }">
                                                                    <input
                                                                    placeholder="HH:mm"
                                                                    @click="togglePopover()"
                                                                    v-model="row.value"
                                                                    :disabled="isEditMode" 
                                                                    class="form-control"
                                                                    />
                                                                    <div @click="togglePopover" class="input-group-append">
                                                                    <span class="input-group-text">
                                                                        <em class="fa fa-clock"></em>
                                                                    </span>
                                                                    </div>
                                                                </template>
                                                            </v-date-picker>
                                                            
                                                            <!-- Time picker for valueTo (Between/Date Range) -->
                                                            <v-date-picker mode="time" v-model="row.valueTo" 
                                                            v-if="row.sqlType === 'time' && !isValueHidden(row) && isValueToVisible(row) && !isShowIsWeekDay(row)"
                                                                class="timedate input-group mr-2">
                                                                <template v-slot="{ inputValue, togglePopover }">
                                                                    <input
                                                                    placeholder="HH:mm"
                                                                    @click="togglePopover()"
                                                                    v-model="row.valueTo" 
                                                                    :disabled="isEditMode"
                                                                    class="form-control"
                                                                    />
                                                                    <div @click="togglePopover" class="input-group-append">
                                                                    <span class="input-group-text">
                                                                        <em class="fa fa-clock"></em>
                                                                    </span>
                                                                    </div>
                                                                </template>
                                                            </v-date-picker>
                                                            
                                                            <!-- Select dropdown for select/bool type -->
                                                            <select
                                                                v-if="(row.sqlType === 'select' || row.sqlType === 'bool') && (!isValueHidden(row) && !isValueToVisible(row) && !isMultiSelectOperator(row))"
                                                                v-model="row.value"
                                                                class="form-control p-1"
                                                                :disabled="isEditMode">
                                                                <option value="" disabled>{{ $t("Select") }}</option>
                                                                <option
                                                                    v-for="option in row.valueOptions || []"
                                                                    :key="option.id"
                                                                    :value="option.value">
                                                                    {{ option.name }}
                                                                </option>
                                                            </select>
                                                            
                                                            <!-- Multiselect for multi-select operators -->
                                                            <Multiselect
                                                                v-if="row.sqlType === 'select' && isMultiSelectOperator(row) && !isValueHidden(row)"
                                                                v-model="row.value"
                                                                :options="row.valueOptions || []"
                                                                :multiple="true"
                                                                :close-on-select="false"
                                                                :clear-on-select="false"
                                                                :preserve-search="true"
                                                                :searchable="true"
                                                                :show-no-results="false"
                                                                :internal-search="true" label="name"
                                                                track-by="value"
                                                                :placeholder="$t('Select')"
                                                                :disabled="isEditMode" />
                                                            
                                                            <!-- Weekday checkboxes -->
                                                            <div class="d-flex gap-3 flex-wrap"
                                                                v-if="isShowIsWeekDay(row)">
                                                                <div v-for="day in weekDaysList"
                                                                    :key="day.value + '-' + i"
                                                                    class="custom-control custom-checkbox">
                                                                    <input type="checkbox"
                                                                        :disabled="isEditMode"
                                                                        class="custom-control-input"
                                                                        :id="`chk-${i}-${day.value}`"
                                                                        :checked="getDayChecked(row.daysOfWeek, day.value)"
                                                                        @change="toggleDay(row, day.value)" />
                                                                    <label
                                                                        class="custom-control-label"
                                                                        :for="`chk-${i}-${day.value}`">{{
                                                                            day.label
                                                                        }}</label>
                                                                </div>
                                                            </div>
                                                            
                                                            <!-- Regular input for other types -->
                                                            <input v-model="row.value" class="form-control p-1 mr-2"
                                                                :disabled="isEditMode" placeholder="Value"
                                                                v-if="!isValueHidden(row) && row.sqlType != 'select' && row.sqlType != 'date' && row.sqlType != 'time' && row.sqlType != 'bool'" />
                                                            
                                                            <!-- Regular input for valueTo (Between/Date Range) -->
                                                            <input v-model="row.valueTo" class="form-control p-1"
                                                                :disabled="isEditMode" placeholder="Value To"
                                                                v-if="isValueToVisible(row) && row.sqlType != 'select' && row.sqlType != 'date' && row.sqlType != 'time' && row.sqlType != 'bool'" />
                                                        </div>

                                                        <!-- <button type="button" class="btn btn-sm btn-danger" 
                                                                style="border: none; border-radius: 4px; padding: 4px 8px;"
                                                                @click="removeCondition(blockIndex, i)" v-if="block.conditions.length > 1">
                                                            
                                                        </button> -->
                                                        
                                                        <!-- removeCondition -->
                                                     <!-- <a href="" @click="removeCondition(blockIndex, i)" v-if="block.conditions.length > 1" v-show="!isEditMode">
                                                          <em class="fa fa-trash-o text-danger font-20"></em>
                                                     </a> -->
                                                     <a href="" @click="removeCondition(blockIndex, i, $event)" v-if="block.conditions.length > 1" v-show="!isEditMode">
                                                        <em class="fa fa-trash-o text-danger font-20"></em>
                                                     </a>

                                                    </div>
                                                    
                                                    <button type="button" class="btn btn-sm btn-success mt-2" 
                                                            style="border-radius: 4px;"
                                                            @click="addCondition(blockIndex)" v-if="!isEditMode">
                                                        + Add Condition
                                                    </button>
                                                </div>
                                            </div>
                                            
                                           
                                            <!-- Add Block Button - Moved to top -->
                                            <div class="add-block-section" v-if="!isEditMode">
                                               <button type="button" class="btn btn-primary" style="border-radius: 4px; font-weight: 600;" @click="addBlock">
                                                   + Add Block
                                               </button>
                                            </div>
                                        </div>

                                        <div class="col-lg-12 mt-3 mb-3 p-0" v-if="isShowServiceDetail">
                                                        <div class="form-check form-switch col-md-6 mt-0 p-0 form-group">
                                                            <label class="mr-2" for="peakHoursSwitch">{{
                                                                $t('Apply') }} {{ $t('Rate') }}:</label>
                                                                <div class="row">
                                                                    <div class="col-12 float-left">
                                                          <label class="switch">
                                                            <input
                                                                class="form-check-input custom-control-input dynamic"
                                                                type="checkbox"
                                                                id="peakHoursSwitch"
                                                                v-model="form.rateToggle"
                                                                @change="fncApplyRate"
                                                                :disabled="currentMode === 'View'" />
                                                            <span class="slider round" style="cursor: default;">
                                                                <span class="slider-yes">{{ $t('Yes') }}</span>
                                                                <span class="slider-no">{{ $t('No') }}</span>
                                                            </span>
                                                            </label></div>
                                                        </div>
                                                        </div>
                                                        <div class="input-group mt-3" v-if="form.rateToggle && serviceType != 'Dependent'">
                                                            <span>
                                                             <label class="form-check form-check-inline">
                                                                <div class="custom-control custom-radio custom-control-inline">
                                                                    <input type="radio"
                                                                        id="fixedRate"
                                                                        value="true"
                                                                        v-model="form.isLeaf"
                                                                        @change="chkisLeafRule"
                                                                        class="custom-control-input"
                                                                        :disabled="currentMode === 'View'" />
                                                                    <label for="IsLeafNode"
                                                                        class="custom-control-label"
                                                                        v-if="isCheckExtraFeatureUSICBILLING">
                                                                    {{ $t('IsLeafNode') }}
                                                                    </label>
                                                                    <label for="Fixed"
                                                                        class="custom-control-label"
                                                                        v-else>
                                                                    {{ $t('Fixed') }}
                                                                    </label>
                                                                </div>
                                                                </label>
                                                                <!-- <label class="form-check form-check-inline" v-if="serviceType == 'UsageBased'"> -->
                                                         <label class="form-check form-check-inline">
                                                            <div class="custom-control custom-radio custom-control-inline">
                                                                <input type="radio"
                                                                    id="usageBasedRate"
                                                                    value="true"
                                                                    v-model="form.addAdder"
                                                                    @change="onAdderChecked"
                                                                    class="custom-control-input"
                                                                    :disabled="currentMode === 'View'" />
                                                                <label for="AddAdder"
                                                                    class="custom-control-label"
                                                                    v-if="isCheckExtraFeatureUSICBILLING">
                                                                {{ $t('AddAdder') }}
                                                                </label>
                                                                <label for="UsageBased"
                                                                    class="custom-control-label"
                                                                    v-else>
                                                                {{ $t('UsageBased') }}
                                                                </label>
                                                            </div>
                                                            </label>
                                                            </span>
                                                        </div>
                                        </div>

                                                    <!-- Leaf Rule & Adder -->
                                                    <div class="col-12 d-flex p-0"
                                                        v-if="isShowServiceDetail && form.rateToggle && serviceType != 'Dependent'">
                                                        <!-- <div class="col-lg-4">
                                                            <div class="form-group">
                                                                <label v-if="isCheckExtraFeatureUSICBILLING">{{ $t('IsLeafNode') }}:</label>
                                                                <label v-else>{{ $t('Fixed') }}:</label>
                                                                    <div class="custom-control custom-checkbox">
                                                                        <input type="checkbox" v-model="form.isLeaf" @change="chkisLeafRule" class="custom-control-input" id="chkIsLeaf"/>
                                                                        <label class="custom-control-label" for="chkIsLeaf"></label>
                                                                    </div>
                                                            </div>
                                                        </div> -->
                                                        <div class="col-lg-4" v-if="showIsLeafFlag">
                                                            <div class="form-group">
                                                                <label>{{ $t('ClientServiceFlag') }}:</label><br>
                                                                <div class="flag-selector">
                                                                    <span @click="isLeafTogglePopup" class="flag-icon"
                                                                        :style="{ color: form.selectedFlag.color }">
                                                                        <i class="fas fa-flag"></i>
                                                                    </span>

                                                                    <!-- Flag condition popup -->
                                                                    <div v-if="form.showPopup" class="popup">
                                                                        <div class="popup-header">
                                                                            <span>Flag Condition</span>
                                                                            <button @click="form.showPopup = false"
                                                                                class="close-btn"></button>
                                                                        </div>
                                                                        <div class="flags">
                                                                            <span v-for="flag in flags"
                                                                                :key="flag.value" class="flag-option"
                                                                                @click="isLeafSelectFlag(flag)"
                                                                                :style="{ color: flag.color }">
                                                                                <i class="fas fa-flag"></i>
                                                                            </span>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                       <div class="col-lg-4" v-if="showIsLeafWorkCode">
                                                        <div class="form-group">
                                                            <label>{{ $t('RateCode') }}:</label>
                                                            <select v-model="form.workCodeId"
                                                                    class="form-control p-1"
                                                                    :disabled="currentMode === 'View' || !form.isLeaf">
                                                            <option disabled value="">{{ $t("Select") }}</option>
                                                            <option v-for="itm in workCodeList" :key="itm.value" :value="itm.value">
                                                                {{ itm.name }}
                                                            </option>
                                                            </select>
                                                        </div>
                                                        </div>
                                                    </div>

                                                    <div class="col-12 d-flex flex-wrap p-0"
                                                        v-if="isShowServiceDetail && form.rateToggle && serviceType != 'Dependent'">
                                                        <!-- <div class="col-lg-2" v-if="showAdder">
                                                            <div class="form-group">
                                                                <label v-if="isCheckExtraFeatureUSICBILLING">{{ $t('AddAdder') }}:</label>
                                                                <label v-else>{{ $t('UsageBased') }}:</label>
                                                                    <div class="custom-control custom-checkbox">
                                                                        <input type="checkbox" v-model="form.addAdder" @change="onAdderChecked" class="custom-control-input" id="chkIsAdder"/>
                                                                        <label class="custom-control-label" for="chkIsAdder"></label>
                                                                    </div>
                                                            </div>
                                                        </div> -->
                                                        <div class="col-lg-10 p-0" v-if="showAdderType">
                                                            <div class="form-group">
                                                                <!-- Radio Buttons -->
                                                                <div
                                                                    class="custom-control custom-radio custom-control-inline">
                                                                    <input type="radio" class="custom-control-input"
                                                                        @change="onAdderTypeSelected('T','Add')"
                                                                        v-model="form.adderType" value="T"
                                                                        id="adderTier"  :disabled="currentMode === 'View'">
                                                                    <label class="custom-control-label"
                                                                        for="adderTier">{{ $t('TierWiseAdder')
                                                                        }}</label>
                                                                </div>
                                                                <div
                                                                    class="custom-control custom-radio custom-control-inline">
                                                                    <input type="radio" class="custom-control-input"
                                                                        @change="onAdderTypeSelected('P','Add')"
                                                                        v-model="form.adderType" value="P"
                                                                        id="adderPeriodic" :disabled="currentMode === 'View'">
                                                                    <label class="custom-control-label"
                                                                        for="adderPeriodic">{{
                                                                            $t('PeriodicAdder') }}</label>
                                                                </div>
                                                                <div
                                                                    class="custom-control custom-radio custom-control-inline">
                                                                    <input type="radio" class="custom-control-input"
                                                                        @change="onAdderTypeSelected('M','Add')"
                                                                        v-model="form.adderType" value="M"
                                                                        id="adderSlab" :disabled="currentMode === 'View'">
                                                                    <label class="custom-control-label"
                                                                        for="adderSlab">{{ $t('MatchingAdder')
                                                                        }}</label>
                                                                </div>
                                                                <div
                                                                    class="custom-control custom-radio custom-control-inline">
                                                                    <input type="radio" class="custom-control-input"
                                                                        @change="onAdderTypeSelected('C','Add')"
                                                                        v-model="form.adderType" value="C"
                                                                        id="adderCustom" :disabled="currentMode === 'View'">
                                                                    <label class="custom-control-label"
                                                                        for="adderCustom">{{ $t('ProceedingAdder')
                                                                        }}</label>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <!-- Adder Options -->
                                                    <div v-if="showAddAdderTypeTable && isShowServiceDetail && form.rateToggle && serviceType != 'Dependent'"
                                                        class="col-12 p-0">
                                                        <!-- Adder Table -->
                                                        <div class="listing">
                                                            <div class="table-responsive overflowauto">
                                                                <table
                                                                    class="table table-bordered dt-responsive nowrap mb-0 mt-0">
                                                                    <thead class="thead-bg">
                                                                        <tr>
                                                                            <th width="20%">{{ $t('Units') }}</th>
                                                                            <!-- <th width="10%">{{ $t('Field') }}</th> -->
                                                                            <th width="10%">{{ $t('From') }}</th>
                                                                            <th width="10%">{{ $t('To') }}</th>
                                                                            <th width="10%" style="display: none;">{{
                                                                                $t('Amount') }}</th>
                                                                            <th width="10%">{{ $t('RateCode') }}</th>
                                                                            <th width="10%" v-if="showRepeatAfter">{{
                                                                                $t('RepeatAfter') }}</th>
                                                                            <!-- <th width="10%" v-if="showWorkCode">{{
                                                                                $t('RateCode') }}</th > -->
                                                                            <th width="5%" class="text-center"
                                                                                v-if="showFlag"></th>
                                                                            <th width="5%"></th>
                                                                        </tr>
                                                                    </thead>
                                                                    <tbody>
                                                                        
                                                                        <tr v-for="(adder, j) in visibleAdderRows" :key="j">
                                                                            <td>
                                                                            <select
                                                                                v-model="adder.unit"
                                                                                class="form-control p-1"
                                                                                v-if="j == 0"
                                                                               :disabled="currentMode === 'View'"
                                                                                >
                                                                                <option value="" selected>{{ $t("Select") }}</option>
                                                                                <option
                                                                                    v-for="itm in usageMeasurementList"
                                                                                    :key="itm.value"
                                                                                    :value="itm.value"
                                                                                >
                                                                                    {{ itm.name }}
                                                                                </option>
                                                                                </select>
                                                                            </td>
                                                                            <!-- <td >
                                                                                <select v-model="adder.field" class="form-control p-1" v-if="j == 0">
                                                                                    <optgroup v-for="(options, group) in groupedOptions" :label="group" :key="group">
                                                                                        <option
                                                                                        v-for="option in options"
                                                                                        :value="option.value"
                                                                                        :key="option.value"
                                                                                        >
                                                                                        {{ option.label }}
                                                                                        </option>
                                                                                    </optgroup>
                                                                               </select>
                                                                            </td> -->
                                                                           <td>
                                                                                <input type="number"
                                                                                        v-model="adder.from"
                                                                                        class="form-control p-1"
                                                                                        :disabled="currentMode === 'View'" />
                                                                                </td>
                                                                           <td>
                                                                            <input type="text"
                                                                                    @input="onToValueChange($event.target.value, j)"
                                                                                    v-model="adder.to"
                                                                                    :class="['form-control p-1', adder.toInvalid ? 'border-danger' : '']"
                                                                                    :disabled="currentMode === 'View' " />
                                                                            </td>
                                                                            <td style="display: none;"><input
                                                                                    type="number" v-model="adder.amount"
                                                                                    class="form-control p-1"   /></td>
                                                                         <td>
                                                                            <select v-model="adder.workCodeBefore"
                                                                                    class="form-control p-1"
                                                                                    :disabled="currentMode === 'View'">
                                                                                <option value="" disabled selected>{{ $t("Select") }}</option>
                                                                                <option v-for="itm in workCodeList" :key="itm.value" :value="itm.value">
                                                                                {{ itm.name }}
                                                                                </option>
                                                                            </select>
                                                                            </td>
                                                                            <td v-if="showRepeatAfter"><input
                                                                                    type="number"
                                                                                    v-model="adder.repeatAfter"
                                                                                    class="form-control p-1" :disabled="currentMode === 'View'" /></td>
                                                                            <!-- <td v-if="showWorkCode">
                                                                                <select v-model="adder.workCodeAfter"
                                                                                    class="form-control p-1">
                                                                                    <option value="" disabled>{{
                                                                                        $t("Select") }}</option>
                                                                                    <option v-for="itm in workCodeList"
                                                                                        :key="itm.value"
