                                            <!-- <p class="mt-2">If you edit this service it will overwrite all services used in billing rule or used for contract. Click 'Yes' to overwrite all services.Click 'No' if you don't want to overwrite the service and continue using it. In the case of 'No', a clone of the service will be
                                                    created with the entered new name. Else click on 'Cancel' button.</p> -->
                                        </div>
