            <!-- End Modal Dialog -->
        </div>
        <!-- End Modal -->
        <RateCode v-if="rateCode" @CloseAddWorkCode="CloseAddWorkCode"></RateCode>
    </section>
</template>
<script>
import DataService from "../../services/DataService";
import RateCode from "../../views/Billing/RateCode/AddRateCode.vue";
export default {
    components: { RateCode },
    props: {
        serviceDetailForBillingRule: {
            type: Array,
            required: false
        },
        reqFrom: {
            type: String,
            required: false
        },
        conditionId: {
            type: String,
            required: false
        },
        viewType: {
            type: String,
            required: false
        },
        nodeName: {
            type: String,
            required: false
        },
        configurationId: {
            type: String,
            required: false
        },
         serviceRuleItem: {
            type: Array,
            required: false
        },
        reqType: {
            type: String,
            required: false
        },
        serviceId:{
            type: String,
            required: false
        },
        rateData:{
            type: String,
            required: false
        },
        mode: {
   type: String,
    default: 'Edit'
}
    },
    data() {
        return {
            currentMode: '',
            openRuleMode:false,
            isLoading: false,
            isCloneModel: false,
            operator: [],
            FormSchema: [{
                layoutType: 'single',
                Data: [
                    {
                        astype: 'TextField',
                        label: this.$t('ConditionName'),
                        name: 'ServiceName',
                        value: "",
                        placeholder: '',
                        validationRules: {
                            required: true
                        },
                        disabled: false,
                    }
                ]
            }],
            buttons: [
                {
                    type: 'submit',
                    class: 'btn btn-success',
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('Save'),
                    onClick: this.CreateClone
                },
                {
                    type: 'button',
                    class: 'btn btn-danger',
                    text: "<i class='fa fa-close pr-2'></i>" + this.$t('Cancel'),
                    onClick: this.CloseCloneModel
                }
            ],
            form: {
                conditionId: 0,
                conditions: [{ relation: '', openingBracket: '(', field: '', operator: '', value: '', valueTo: '', daysOfWeek: '', closingBracket: ')' }],
                isLeaf: false,
                workCodeId: '',
                addAdder: false,
                addAdderTypeTable: false,
                adderType: '',
                adderRows: [{
                    unit: '',
                    // field: '', 
                    from: 0, to: '', toInvalid: false, workCodeBefore: '', repeatAfter: '', workCodeAfter: '', selectedFlag: { value: 'N', color: 'grey' }, showPopup: false,statusId:1001
                }],
                selectedFlag: { value: 'N', color: 'grey' },
                showPopup: false,
                rateToggle: false,
                isUpdateRule: false,
                dependentOnServices: null,
                dependentType: '',
                dependentPrice: 0,
                functionType: '',
                serviceId:0
            },
            conditionBlocks: [
                {
                    operator: 'AND', // within-block operator
                    joinWithPrevious: 'OR', // operator between this block and the previous block
                    conditions: [
                        { field: '', operator: '', value: '', valueTo: '', daysOfWeek: '', operatorList: [], valueOptions: [], sqlType: '' }
                    ]
                }
            ],
            serviceName: '',
            showAdder: false,
            conditionName: '',
            showError: false,
            showAdderType: false,
            serviceType: '',
            billingFrequency: '',
            showRepeatAfter: false,
            showWorkCode: false,
            showFlag: false,
            usageMeasurementList: [],
            workCodeList: [],
            meterReadingList: [],
            companyClientList: [],
            groupedOptions: {},
            expandedGroups: {},
            showIsLeafFlag: false,
            showIsLeafWorkCode: false,
            configurationId: 0,
            isEditMode: false,
            showAddAdderTypeTable: false,
            isCheckExtraFeatureUSICBILLING: false,
             isRateCodeDisabled: false,
            weekDaysList: [
                { label: 'Sun', value: '1' },
                { label: 'Mon', value: '2' },
                { label: 'Tue', value: '3' },
                { label: 'Wed', value: '4' },
                { label: 'Thu', value: '5' },
                { label: 'Fri', value: '6' },
                { label: 'Sat', value: '7' },
            ],
            flags: [
                { value: 'R', color: 'red' },
                { value: 'Y', color: 'gold' },
                { value: 'G', color: 'green' },
                { value: 'N', color: 'grey' },
            ],
            isShowServiceDetail: false,
            rateCode: false,
            isRateCode: false,
            getRuleConditions:[],
            getServiceId:0,
            serviceDdlList: [],
            planId:0
            // ,displayServiceDetail:false
        };
    },
mounted() {
    
    for (const group in this.groupedOptions) {
        this.$set(this.expandedGroups, group, true);
    }
    debugger
     if (this.mode === true || this.mode === 'true' || this.mode === 'View') {
    this.currentMode = 'View';
  } else if (this.mode === false || this.mode === 'false' || this.mode === 'Edit') {
    this.currentMode = 'Edit';
  } else {
    this.currentMode = 'Add';
  }

  console.log(" Mounted Mode:", this.currentMode);
},
    computed: {
        isMultiple() {
            return this.form.dependentType === 'M';
        },
         isViewMode() {
            debugger
    return this.mode === true || String(this.mode).toLowerCase() === 'view';
  },
        flatGroupedOptions() {
            const result = [];
            for (const groupName in this.groupedOptions) {
                result.push({ isGroup: true, groupName });
                if (this.expandedGroups[groupName]) {
                    const fields = this.groupedOptions[groupName];
                    fields.forEach(field => {
                        result.push({ ...field, isGroup: false, groupName });
                    });
                }
            }
            return result;
        },
        fieldLabelMap() {
            const map = {};
            Object.values(this.groupedOptions).flat().forEach(item => {
                map[item.value] = this.$t(item.label);
            });
            return map;
        },
        fieldSqlConditionMap() {
            const map = {};
            Object.values(this.groupedOptions).flat().forEach(item => {
                map[item.value] = item.joinFieldName;
            });
            return map;
        },
        conditionText() {
            return this.conditionBlocks.map((block, blockIndex) => {
                const blockConditions = block.conditions
                    .filter(condition => {
                        // Show condition as soon as field is selected
                        return condition.field;
                    })
                    .map((condition, conditionIndex) => {
                        const field = this.fieldLabelMap[condition.field] || condition.field || '';
                        
                        // Progressive display based on what's selected
                        if (!condition.field) {
                            return '[Select Field]';
                        }
                        
                        if (!condition.operator) {
                            return `${field} [Select Operator]`;
                        }
                        
                        let operatorDisplay = '';
                        if (condition.operatorList && Array.isArray(condition.operatorList)) {
                            const op = condition.operatorList.find(o => o.OPERATOR_ID === condition.operator);
                            operatorDisplay = op ? op.OPERATOR_DISPLAY_NAME : condition.operator;
                        } else {
                            operatorDisplay = '[Loading Operators...]';
                        }
                        var value = '';
                        const operatorName = operatorDisplay?.toLowerCase();
                        
                        // Check if this operator needs a value
                        const noValueOperators = ['is empty', 'is not empty', 'is off day', 'is duplicate'];
                        const needsValue = !noValueOperators.includes(operatorName);
                        
                        if (!needsValue) {
                            // Operators that don't need values - show empty
                            value = '';
                        }
                        else if (condition.sqlType == 'select' && (operatorName !== 'not equal to one of multiple possible values' && operatorName !== 'equal to one of multiple possible values')) {
                            const ddlItem = condition.valueOptions?.find(o => o.value === condition.value);
                            value = ddlItem ? ddlItem.name : (condition.value || '[Select Value]');
                        }
                        else if (condition.sqlType == 'select' && (operatorName === 'not equal to one of multiple possible values' || operatorName === 'equal to one of multiple possible values')) {
                            if (condition.value && Array.isArray(condition.value) && condition.value.length > 0) {
                            const values = Array.isArray(condition.value) ? condition.value : [condition.value];
                            const selectedNames = values.map(obj => {
                                const match = condition.valueOptions?.find(opt => opt.value === obj.value);
                                return match ? match.name : obj.name || obj.value;
                            });
                            value = selectedNames.join(', ');
                            } else {
                                value = '[Select Values]';
                            }
                        }
                        else if (condition.sqlType === 'bool') {
                            const ddlItem = condition.valueOptions?.find(o => o.value === condition.value);
                            value = ddlItem ? ddlItem.name : (condition.value || '[Select Value]');
                        }
                        else if (condition.sqlType == 'date') {
                            if (condition.value && condition.value != "") {
                            if (typeof (condition.value) === 'string') {
                                condition.value = new Date(condition.value);
                            }
                            value = `${this.$d(condition.value)}` || '';
                            } else {
                                value = '[Select Date]';
                        }
                        }
                        else if (condition.sqlType == 'time') {
                            if (condition.value && condition.value != "") {
                            const timeFormatRegex = /^(0?[1-9]|1[0-2]):[0-5][0-9] (AM|PM)$/i;
                            if (!timeFormatRegex.test(condition.value)) {
                                const time = new Date(condition.value);
                                const formattedTime = time.toLocaleTimeString('en-US', {
                                    hour: '2-digit',
                                    minute: '2-digit',
                                    hour12: true
                                });
                                condition.value = formattedTime;
                                value = condition.value ?? '';
                            }
                            else{
                                value = condition.value;
                                }
                            } else {
                                value = '[Select Time]';
                            }
                        }
                        else {
                            value = condition.value ?? '[Enter Value]';
                        }

                        var valueTo = '';
                        if (operatorDisplay?.toLowerCase() === 'between' || operatorDisplay?.toLowerCase() === 'date range') {
                            if (condition.sqlType == 'date') {
                                if (condition.valueTo && condition.valueTo != "") {
                                if (typeof (condition.valueTo) === 'string') {
                                    condition.valueTo = new Date(condition.valueTo);
                                }
                                valueTo = ' AND' + ' ' + `${this.$d(condition.valueTo)}` || '';
                                } else {
                                    valueTo = ' AND [Select Date]';
                            }
                            }
                            else if (condition.sqlType == 'time') {
                                if (condition.valueTo && condition.valueTo != "") {
                                const timeFormatRegex = /^(0?[1-9]|1[0-2]):[0-5][0-9] (AM|PM)$/i;
                                if (!timeFormatRegex.test(condition.valueTo)) {
                                    const time = new Date(condition.valueTo);
                                    const formattedTime = time.toLocaleTimeString('en-US', {
                                        hour: '2-digit',
                                        minute: '2-digit',
                                        hour12: true
                                    });
                                    condition.valueTo = formattedTime;
                                    valueTo = ' AND' + ' ' + condition.valueTo ?? '';
                                }
                                else{
                                    valueTo = ' AND' + ' ' + condition.valueTo ?? '';
                                }
                                } else {
                                    valueTo = ' AND [Select Time]';
                            }
                            }
                            else if (condition.sqlType != 'date' && condition.sqlType != 'time') {
                                if (condition.valueTo && condition.valueTo != "") {
                                    valueTo = ' AND ' + condition.valueTo;
                                } else {
                                    valueTo = ' AND [Enter Value]';
                                }
                            }
                        }

                        var weekDays = '';
                        if (operatorDisplay?.toLowerCase().trim() === 'is week end') {
                            const selectedDays = (condition.daysOfWeek || '').split(',').filter(Boolean);
                            if (selectedDays.length > 0) {
                            const dayLabels = selectedDays.map(dayValue => {
                                const day = this.weekDaysList.find(d => d.value === dayValue);
                                return day ? day.label : dayValue;
                            });
                            weekDays = ` (${dayLabels.join(', ')})`;
                            } else {
                                weekDays = ' ([Select Days])';
                            }
                        }

                        const relation = conditionIndex > 0 ? ` ${block.operator || 'AND'} ` : '';
                        return `${relation}${field} ${operatorDisplay} ${value}${valueTo}${weekDays}`;
                    })
                    .join('');

                if (blockConditions) {
                    return `(${blockConditions})`;
                }
                return '';
            }).filter(block => block).map((text, idx, arr) => {
                if (idx === 0) return text;
                const joinOp = this.conditionBlocks[idx].joinWithPrevious || 'OR';
                return `${joinOp === 'AND' ? ' AND ' : ' OR '}${text}`;
            }).join('');
        },
         // Filter out deleted rows
        visibleAdderRows() {
            return this.form.adderRows.filter((row) => row.statusId !== 1003);
        },
    },
    created: async function () {
        
        var vm = this;
        const url = window.location.pathname; 
        if (url.includes('/ManagePlan/')) {
            vm.planId = parseInt(this.$route.params.id);
        }
        if (this.serviceRuleItem && this.serviceRuleItem.ruleCondition) {
            this.form.selectedFlag = this.serviceRuleItem.ruleCondition.selectedFlag ? this.serviceRuleItem.ruleCondition.selectedFlag : { value: 'N', color: 'grey' };
            this.form.workCodeId = this.serviceRuleItem.ruleCondition.rateCode ? this.serviceRuleItem.ruleCondition.rateCode : '';
        }
        vm.configurationId = this.configurationId;
        await  vm.GetRuleListingByServiceId(vm.conditionId);
        await vm.GetBillingRuleFieldList();
        vm.BindWorkCode();
        
        // Initialize work codes for default selected rate code
        if (vm.reqFrom === "addMapBillingRuleForService" || vm.reqFrom === "EditMapBillingRuleForService" || vm.reqFrom === "ViewBillingConditionForService") {
            vm.isShowServiceDetail = true;
            vm.isRateCode = true;
            if (vm.reqFrom === "EditMapBillingRuleForService") {
                setTimeout(function () {
                    vm.isEditMode = true;
                    vm.BindRuleConditions(vm.conditionId);
                }, 200);
            }
            else if(vm.reqFrom === "ViewBillingConditionForService" ){
                setTimeout(function () {
                    vm.isEditMode = true;
                    vm.BindRuleConditions(vm.conditionId);
                }, 200);
            }
        }
        else if (vm.reqFrom === "fromBillingService") {
            vm.isEditMode = false;
            await vm.BindRuleConditions(vm.conditionId);
            vm.isShowServiceDetail = false;
        }
        else if (vm.reqFrom === "addBillingService") {
            vm.isEditMode = false;
            vm.isShowServiceDetail = false;
        }
        vm.toggleGroup();
        vm.checkSlabCondition();
        vm.GetAddonMapByServiceId();
        vm.syncConditionBlocks();
    },
    methods: {
        fncApplyRate() {
            console.log("rateToggle", this.form.rateToggle)
        },
        async GetRuleListingByServiceId(SericeId){
            var vm=this;
            var url=`serviceId=${SericeId > 0 ? SericeId : 0}`;
            DataService.GetRuleListingByServiceId(url).then(response=>{
                if(response.data != null && response.data !="[]"){
                    vm.getRuleConditions =JSON.parse(response.data);
                }
                else{
                    vm.getRuleConditions=[];
                }
            })
        },
        RateCodeModel() {
            var vm = this;
            vm.rateCode = true;
        },
        CloseAddWorkCode() {
            this.rateCode = false;
            this.BindWorkCode();
        },
        getDayChecked(valueString, dayValue) {
            if (!valueString) return false;
            return valueString.split(',').includes(dayValue);
        },
        toggleDay(row, dayValue) {
            let current = (row.daysOfWeek || "").split(',').filter(Boolean);
            const index = current.indexOf(dayValue);

            if (index >= 0) {
                current.splice(index, 1); // uncheck
            } else {
                current.push(dayValue); // check
            }

            row.daysOfWeek = current.join(',');
        },
        isMultiSelectOperator(row) {
            const selectedOperator = (row.operatorList || []).find(op => op.OPERATOR_ID === row.operator);
            if (!selectedOperator) return false;
            const name = selectedOperator.OPERATOR_DISPLAY_NAME?.toLowerCase();
            return name === 'equal to one of multiple possible values' || name === 'not equal to one of multiple possible values';
        },
        isValueHidden(row) {
            const selectedOperator = (row.operatorList || []).find(
                op => op.OPERATOR_ID === row.operator
            );
            if (!selectedOperator) return false;

            const operatorName = selectedOperator.OPERATOR_DISPLAY_NAME?.toLowerCase();
            return operatorName === 'is empty' || operatorName === 'is not empty' || operatorName === 'is off day' || operatorName === 'is duplicate';
        },
        isShowIsWeekDay(row) {
            const selectedOperator = (row.operatorList || []).find(
                op => op.OPERATOR_ID === row.operator
            );
            if (!selectedOperator) return false;

            const operatorName = selectedOperator.OPERATOR_DISPLAY_NAME?.toLowerCase();
            return operatorName === 'is week end';
        },
        isValueToVisible(row) {
            if (!row.operator) return false;  // hide if no operator selected

            const selectedOperator = (row.operatorList || []).find(
                op => op.OPERATOR_ID === row.operator
            );
            if (!selectedOperator) return false;

            const displayName = selectedOperator.OPERATOR_DISPLAY_NAME?.toLowerCase();
            return displayName === 'between' || displayName === 'date range';
        },
