            const lastRow = activeRows.at(-1);

            // Base object template for new rows
            const newRowTemplate = {
                unit: '',
                to: '',
                workCodeBefore: '',
                toInvalid: false,
                repeatAfter: '',
                workCodeAfter: '',
                selectedFlag: { value: 'N', color: 'grey' },
                showPopup: false,
                statusId: 1001, // active
            };

            if (lastRow && lastRow.from >= 0) {
                if (lastRow.to === '') {
                    lastRow.toInvalid = true;
                    return;
                }
                lastRow.toInvalid = false;

                this.form.adderRows.push({
